.pokecard{
    width:300px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
    text-align:center;
    background-color: white;
    border-radius: 3px;
}
.pokecard-data{
    font-size: 80%;
}
.pokecard-image{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: #eceff1;
    border-radius: 3px;
    margin: 1rem;
}
.pokecard-title{
    margin-bottom: 1rem;
    font-weight: bold;
    font-size:1.5rem;
}
.pokecard img{
    transition: all ease 500ms;
}
.pokecard img:hover{
    transform: scale(1.25);
    opacity:0.7;

}